.Typing {
  width: 60%;
  height: 100vh;
  margin: auto;
}

.Typing h1 {
  font-family: 'Lobster', cursive;
  margin-top: 0;
  padding-top: 10vh;
  color: rgba(0,212,255,1);
  font-size: 2.5rem;
}

.Typing p {
  font-family: 'Roboto Mono', monospace;
  margin-top: 0;
  padding-top: 3vh;
  color: rgba(0,212,255,1);
  font-size: 1rem;
  margin: auto;
}

.Typing .blinkingCursor {
  font-weight: 900;
  font-size: 30px;
  color: #ddd;
  -webkit-animation: 750ms blink step-end infinite;
  -moz-animation: 750ms blink step-end infinite;
  -ms-animation: 750ms blink step-end infinite;
  -o-animation: 750ms blink step-end infinite;
  animation: 750ms blink step-end infinite;
}

@keyframes blink {
  from, to {
    color: transparent;
  }
  50% {
    color: #ddd;
  }
}

@-moz-keyframes blink {
  from, to {
    color: transparent;
  }
  50% {
    color:#ddd;
  }
}

@-webkit-keyframes blink {
  from, to {
    color: transparent;
  }
  50% {
    color: #ddd;
  }
}

@-ms-keyframes blink {
  from, to {
    color: transparent;
  }
  50% {
    color: #ddd;
  }
}

@-o-keyframes blink {
  from, to {
    color: transparent;
  }
  50% {
    color: #ddd;
  }
}

.DisplayedText {
  font-family: 'Roboto Mono', monospace;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  font-size: 1.3rem;
  color: #ddd;
}

.InputField {
  margin: 1.5rem;
}

.InputField input {
    padding: 1rem 2rem;
    font-size: 1.3rem;
}

.ButtonContainer {
  margin-top: 2rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.ButtonContainer button {
  font-family: 'Lobster', cursive;
  padding:0.35em 1.2em;
  border:0.1em solid #FFFFFF;
  margin:0 0.3em 0.3em 0;
  border-radius:0.12em;
  box-sizing: border-box;
  text-decoration:none;
  font-weight:300;
  color:#000;
  text-align:center;
  transition: all 0.2s;
}

.ButtonContainer button:hover {
  background-color: rgba(0,212,255,1);
  border:0.1em solid rgba(0,212,255,1);
}

.ButtonContainer h3 {
  color: #ddd;
}
